@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap");

$font-family: "Open Sans";
$color-background-body: #73b7e1;
$color-background-nav: rgba(256, 256, 256, 0.5);
$color-background-btn: #d469a5;
$color-main: white;
$color-link: #472063;

body {
  font-family: $font-family;
  background-color: $color-background-body;
  color: $color-main;
  margin: 0 auto;
}

.half-column {
  display: inline-block;
  width: 40%;
  padding: 50px 0px 10px 20px;

  .right {
    float: right;
  }

  .cta-button {
    border: 2px solid white;
    border-radius: 50px;
    background-color: $color-background-btn;
    color: $color-main;
    font-weight: 700;
    padding: 5px 20px;
    text-decoration: none;
  
    &:hover {
      background-color: #fa75ad;
    }
  }
}

.landing-copy {
  margin-left: 70px;

  p {
    font-size: 1.3em;
    line-height: 1.4em;
  }
  h1 {
    font-size: 3em;
    line-height: 1.2em;
  }
}

nav {
  text-align: center;
  margin-top: 6em;
  background-color: $color-background-nav;
  height: 3.75em;
  padding: 0px 40px;
}

.main-nav {
  li {
    display: inline-block;
    padding: 5px 30px;
    font-weight: 400;
    font-size: 1.2em;
    margin: 10px;

    a {
      color: $color-link;
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}


